'use strict';

import {JsonPointer} from '../../utils/JsonPointer';
import {RedocComponent, BaseComponent} from '../base';
import ParamsList from '../ParamsList/params-list';
import ResponsesList from '../ResponsesList/responses-list';
import ResponsesSamples from '../ResponsesSamples/responses-samples';
import SchemaSample from '../SchemaSample/schema-sample';

@RedocComponent({
  selector: 'method',
  template: `
    <div class="method">
      <div class="method-content">
        <h2 class="method-header sharable-header">
          <a class="share-link" href="#{{tag}}{{pointer}}"></a>{{data.methodInfo.summary}}
        </h2>
        <h3 class="method-endpoint">
          <span class="http-method" [ngClass]="data.httpMethod">{{data.httpMethod}}</span>
          <span class="api-url">{{data.apiUrl}}</span> <span class="path">{{data.path}}</span>
        </h3>
        <div class="method-tags" *ngIf="data.methodInfo.tags.length">
          <a *ngFor="#tag of data.methodInfo.tags" attr.href="#{{tag}}"> {{tag}} </a>
        </div>
        <p *ngIf="data.methodInfo.description" class="method-description"
          innerHtml="{{data.methodInfo.description | marked}}">
        </p>
        <params-list pointer="{{pointer}}/parameters"> </params-list>
        <responses-list pointer="{{pointer}}/responses"> </responses-list>
      </div>
      <div class="method-samples">
        <div *ngIf="data.bodyParam">
          <header> Body sample </header>
          <schema-sample pointer="{{data.bodyParam._pointer}}/schema"> </schema-sample>
        </div>
        <div>
          <responses-samples pointer="{{pointer}}/responses"> </responses-samples>
        </div>
      </div>
    <div>
  `,
  styles: [`
    .share-link {
      cursor: pointer;
      margin-left: -15px;
      padding: 0;
      line-height: 1;
      width: 15px;
      display: inline-block; }

    .share-link:before {
      content: "";
      width: 15px;
      height: 15px;
      background-size: contain;
      background-image: url("data:image/svg+xml;base64,");
      opacity: 0.5;
      visibility: hidden;
      display: inline-block;
      vertical-align: middle; }

    .sharable-header:hover .share-link:before, .share-link:hover:before {
      visibility: visible; }

    :host {
      padding-bottom: 100px;
      display: block;
      border-bottom: 2px solid rgba(127, 127, 127, 0.25); }

    responses-list, params-list {
      display: block; }

    .method-header {
      font-size: 25px;
      font-weight: 200;
      color: #253137; }

    .method-endpoint {
      margin: 0;
      font-weight: 200;
      font-size: 0; }

    .method-endpoint > span {
      padding-top: 3px;
      padding-bottom: 3px;
      vertical-align: middle;
      font-size: 14px; }

    .api-url {
      color: #999;
      margin-left: 10px; }

    .path {
      font-family: monospace;
      font-weight: bold;
      color: #00329F; }

    .method-tags {
      margin-top: 20px; }

    .method-tags a {
      font-size: 16px;
      color: #999;
      display: inline-block;
      padding: 0 0.5em;
      text-decoration: none; }

    .method-tags a:before {
      content: '#';
      margin-right: -0.4em; }

    .method-tags a:first-of-type {
      padding: 0; }

    .method-content, .method-samples {
      display: block;
      box-sizing: border-box;
      float: left; }

    .method-content {
      width: 60%;
      padding: 0 20px; }

    .method-samples {
      color: #CFD2D3;
      width: 40%;
      padding: 10px 20px;
      background: #263238; }

    responses-samples {
      display: block; }

    .method-samples header {
      font-size: 16px;
      margin: 5px 0;
      color: #8A9094;
      text-transform: uppercase; }

    .method-samples schema-sample {
      display: block; }

    .method:after {
      content: "";
      display: table;
      clear: both; }

    .method-description {
      padding: 30px 0; }

    .http-method {
      color: white;
      background-color: #1976D3;
      padding: 3px 10px;
      text-transform: uppercase; }

    .http-method.delete {
      background-color: red; }

    .http-method.post {
      background-color: #00329F; }

    .http-method.patch {
      background-color: orange; }

    .http-method.put {
      background-color: crimson; }

    .http-method.options {
      background-color: black; }

    .http-method.head {
      background-color: darkkhaki; }

    @media (max-width: 1100px) {
      .methods:before {
        display: none; }
      .method-samples, .method-content {
        width: 100%; }
      .method-samples {
        margin-top: 2em; }
      :host {
        padding-bottom: 0; } }
  `],
  directives: [ParamsList, ResponsesList, ResponsesSamples, SchemaSample],
  inputs: ['tag']
})
export default class Method extends BaseComponent {
  constructor(schemaMgr) {
    super(schemaMgr);
  }

  prepareModel() {
    this.data = {};
    this.data.apiUrl = this.schemaMgr.apiUrl;
    this.data.httpMethod = JsonPointer.baseName(this.pointer);
    this.data.path = JsonPointer.baseName(this.pointer, 2);
    this.data.methodInfo = this.componentSchema;
    this.data.methodInfo.tags = this.filterMainTags(this.data.methodInfo.tags);
    this.data.bodyParam = this.findBodyParam();
  }

  filterMainTags(tags) {
    var tagsMap = this.schemaMgr.getTagsMap();
    if (!tags) return [];
    return tags.filter(tag => tagsMap[tag] && tagsMap[tag]['x-traitTag']);
  }

  findBodyParam() {
    let pathParams = this.schemaMgr.getMethodParams(this.pointer, true);
    let bodyParam = pathParams.find(param => param.in === 'body');
    return bodyParam;
  }
}
