'use strict';

import {RedocComponent, BaseComponent} from '../base';
import Method from '../Method/method';

@RedocComponent({
  selector: 'methods-list',
  template: `
    <div class="methods">
      <div class="tag" *ngFor="#tag of data.tags">
        <div class="tag-info" [attr.tag]="tag.name">
          <h1 class="sharable-header"> <a class="share-link" href="#{{tag.name}}"></a>{{tag.name}} </h1>
          <p *ngIf="tag.description" innerHtml="{{ tag.description | marked }}"> </p>
        </div>
        <method *ngFor="#method of tag.methods" [pointer]="method.pointer" [attr.pointer]="method.pointer"
        [attr.tag]="method.tag" [tag]="method.tag"></method>
      </div>
    </div>
  `,
  styles: [`
    .share-link {
      cursor: pointer;
      margin-left: -15px;
      padding: 0;
      line-height: 1;
      width: 15px;
      display: inline-block; }

    .share-link:before {
      content: "";
      width: 15px;
      height: 15px;
      background-size: contain;
      background-image: url("data:image/svg+xml;base64,");
      opacity: 0.5;
      visibility: hidden;
      display: inline-block;
      vertical-align: middle; }

    .sharable-header:hover .share-link:before, .share-link:hover:before {
      visibility: visible; }

    .tag-info {
      padding: 0 20px;
      box-sizing: border-box;
      background-color: white; }

    .tag-info:after, .tag-info:before {
      content: "";
      display: table; }

    .tag-info h1 {
      color: #00329F;
      text-transform: capitalize;
      font-weight: bold; }

    .methods {
      display: block;
      position: relative; }

    .methods:before {
      content: "";
      background: #263238;
      height: 100%;
      width: 40%;
      top: 0;
      right: 0;
      position: absolute;
      z-index: -1; }

    @media (max-width: 1100px) {
      .methods:before {
        display: none; } }
  `],
  directives: [Method]
})
export default class MethodsList extends BaseComponent {

  constructor(schemaMgr) {
    super(schemaMgr);
  }

  prepareModel() {
    this.data = {};
    // follow SwaggerUI behavior for cases when one method has more than one tag:
    // duplicate methods

    let menuStructure = this.schemaMgr.buildMenuTree();
    let tags = Array.from(menuStructure.entries())
      .map((entry) => {
        let [tag, {description, methods}] = entry;
        // inject tag name into method info
        methods = methods || [];
        methods.forEach(method => {
          method.tag = tag;
        });
        return {
          name: tag,
          description: description,
          methods: methods
        };
      });
    this.data.tags = tags;
    // TODO: check $ref field
  }
}
