'use strict';

import {Component, View} from 'angular2/core';
import {CORE_DIRECTIVES} from 'angular2/common';

@Component({
  selector: 'tabs'
})
@View({
  template: `
    <ul>
      <li *ngFor="#tab of tabs" [ngClass]="{active: tab.active}" (click)="selectTab(tab)"
        class="tab-{{tab.tabStatus}}">{{tab.tabTitle}}</li>
    </ul>
    <ng-content></ng-content>
  `,
  directives: [CORE_DIRECTIVES],
  styles: [`
    ul {
      display: block;
      margin: 0;
      padding: 0; }

    li {
      list-style: none;
      display: inline-block;
      cursor: pointer; }

    .tab-success:before, .tab-error:before, .tab-redirect:before, .tab-info:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: 6px;
      width: 6px;
      border-radius: 50%;
      margin-right: 0.5em; }

    .tab-success:before {
      box-shadow: 0 0 3px 0 #00aa11;
      background-color: #00aa11; }

    .tab-error:before {
      box-shadow: 0 0 3px 0 #E53935;
      background-color: #E53935; }

    .tab-redirect:before {
      box-shadow: 0 0 3px 0 #F88F00;
      background-color: #F88F00; }

    .tab-info:before {
      box-shadow: 0 0 3px 0 #66C2FF;
      background-color: #66C2FF; }
  `]
})
export class Tabs {
  constructor() {
    this.tabs = [];
  }

  selectTab(tab) {
    this.tabs.forEach((tab) => {
      tab.active = false;
    });
    tab.active = true;
  }

  addTab(tab) {
    if (this.tabs.length === 0) {
      tab.active = true;
    }
    this.tabs.push(tab);
  }
}

@Component({
  selector: 'tab',
  inputs: ['tabTitle', 'tabStatus']
})
@View({
  template: `
    <div class="tab-wrap" [hidden]="!active">
      <ng-content></ng-content>
    </div>
  `
})
export class Tab {
  constructor(tabs) {
    this.active = false;
    tabs.addTab(this);
  }
}

Tab.parameters = [ [ Tabs ] ];
