'use strict';

import {RedocComponent, BaseComponent} from '../base';
import JsonPointer from '../../utils/JsonPointer';
import {Tabs, Tab} from '../../common/components/Tabs/tabs';
import SchemaSample from '../SchemaSample/schema-sample';
import {statusCodeType} from '../../utils/helpers';


function isNumeric(n) {
  return (!isNaN(parseFloat(n)) && isFinite(n));
}

function hasExample(response) {
  return ((response.examples && response.examples['application/json']) ||
    response.schema);
}

@RedocComponent({
  selector: 'responses-samples',
  template: `
    <header *ngIf="data.responses.length"> Responses samples </header>
    <tabs *ngIf="data.responses.length">
      <tab *ngFor="#response of data.responses" tabTitle="{{response.code}} {{response.description}}"
        [tabStatus]="response.type">
        <schema-sample [pointer]="response.pointer"></schema-sample>
      </tab>
    </tabs>
  `,
  styles: [`
    tab, tabs {
      display: block; }

    schema-sample {
      display: block; }

    header {
      font-size: 16px;
      margin: 5px 0;
      color: #8A9094;
      text-transform: uppercase; }

    :host tabs li {
      font-size: 13px;
      margin: 2px 0;
      padding: 2px 5px;
      color: #8A9094;
      line-height: 1.25;
      color: #8A9094; }
      :host tabs li.active {
        background-color: white;
        color: #666; }

    :host tabs ul {
      padding-top: 10px; }
  `],
  directives: [SchemaSample, Tabs, Tab]
})
export default class ResponsesSamples extends BaseComponent {
  constructor(schemaMgr) {
    super(schemaMgr);
  }

  prepareModel() {
    this.data = {};
    this.data.responses = [];

    let responses = this.componentSchema;
    if (!responses) return;

    responses = Object.keys(responses).filter(respCode => {
      // only response-codes and "default"
      return ( isNumeric(respCode) || (respCode === 'default'));
    }).map(respCode => {
      let resp = responses[respCode];
      resp.pointer = JsonPointer.join(this.pointer, respCode);
      if (resp.$ref) {
        let ref = resp.$ref;
        resp = this.schemaMgr.byPointer(resp.$ref);
        resp.pointer = ref;
      }

      resp.code = respCode;
      resp.type = statusCodeType(resp.code);
      return resp;
    })
    .filter(response => hasExample(response));
    this.data.responses = responses;
  }
}
