'use strict';

import {RedocComponent, BaseComponent} from '../base';
import JsonSchema from '../JsonSchema/json-schema';

@RedocComponent({
  selector: 'params-list',
  template: `
    <h2 class="param-list-header" *ngIf="data.params.length"> Parameters </h2>
    <div class="params-wrap">
      <div *ngFor="#param of data.params" class="param">
          <div class="param-name">
            <span> {{param.name}} </span>
          </div>
          <div class="param-info">
            <div>
              <span class="param-type" [ngClass]="param.type">{{param.type}}</span>
              <span *ngIf="param.required" class="param-required">Required</span>
            </div>
            <div class="param-description" innerHtml="{{param.description | marked}}"></div>
          </div>
      </div>
    </div>

    <div *ngIf="data.bodyParam">
      <h2 class="param-list-header" *ngIf="data.bodyParam"> Request Body </h2>

      <div class="body-param-description" innerHtml="{{data.bodyParam.description | marked}}"></div>
      <div>
        <json-schema pointer="{{data.bodyParam.pointer}}/schema">
        </json-schema>
      </div>
    </div>
  `,
  styles: [`
    .param-list-header {
      border-bottom: 1px solid #999;
      font-size: 18px;
      padding: 0.2em 0;
      margin: 0.5em 0;
      color: #253137; }

    .param-schema {
      padding-left: 12.5px;
      border-left: 1px solid #7D97CE; }

    .param-wrap {
      position: relative; }

    .param-schema:before {
      content: "";
      position: absolute;
      left: 13.5px;
      top: 20px;
      bottom: 0;
      border-left: 1px solid #7D97CE; }

    .param-name {
      font-size: 14px;
      padding: 10px 25px 10px 0;
      font-weight: bold;
      box-sizing: border-box;
      line-height: 20px;
      border-left: 1px solid #7D97CE;
      white-space: nowrap;
      position: relative; }

    .param-info {
      width: 100%;
      padding: 10px 0;
      box-sizing: border-box;
      border-bottom: 1px solid #ccc; }

    .param {
      display: flex; }

    .param-required {
      color: red;
      font-weight: bold;
      font-size: 12px;
      line-height: 20px;
      vertical-align: middle; }

    .param-type {
      text-transform: capitalize;
      color: #999;
      font-size: 12px;
      line-height: 20px;
      vertical-align: middle;
      font-weight: bold; }

    .param-type-trivial {
      margin: 10px 10px 0;
      display: inline-block; }

    /* tree */
    .param-name > span:before {
      content: "";
      display: inline-block;
      width: 7px;
      height: 7px;
      background-color: #7D97CE;
      margin: 0 10px;
      vertical-align: middle; }

    .param-name > span:after {
      content: "";
      position: absolute;
      border-top: 1px solid #7D97CE;
      width: 10px;
      left: 0;
      top: 20px; }

    .param-wrap:first-of-type .param-name:before {
      content: "";
      display: block;
      position: absolute;
      left: -1px;
      top: 0;
      border-left: 2px solid white;
      height: 20px; }

    .param-wrap:last-of-type > .param > .param-name:after {
      content: "";
      display: block;
      position: absolute;
      left: -2px;
      border-left: 2px solid white;
      top: 21px;
      background-color: white;
      bottom: 0; }

    .param-wrap:last-of-type > .param-schema {
      border-left-color: transparent; }

    .param-schema .param-wrap:first-of-type .param-name:before {
      display: none !important; }

    /* styles for array-schema for array */
    .params-wrap.params-array:before, .params-wrap.params-array:after {
      display: block;
      font-weight: bold;
      color: #999;
      font-size: 12px;
      line-height: 1.5; }

    .params-wrap.params-array:after {
      content: "]"; }

    .params-wrap.params-array:before {
      content: "Array [";
      padding-top: 1em; }

    .params-wrap.params-array {
      padding-left: 10px; }

    .param-schema.param-array:before {
      bottom: 9px;
      width: 10px;
      border-left-style: dashed;
      border-bottom: 1px dashed #7D97CE; }

    .params-wrap.params-array > .param-wrap:first-of-type > .param > .param-name:after {
      content: "";
      display: block;
      position: absolute;
      left: -1px;
      top: 0;
      border-left: 2px solid white;
      height: 20px; }

    .param.discriminator > div {
      padding-bottom: 0;
      border-bottom: 0; }

    .discriminator-info {
      font-weight: bold; }

    :host tabs {
      display: block;
      border-left: 1px solid #7D97CE; }

    :host tabs li {
      margin: 0.2em 0.5em 0.2em 0;
      font-size: 14px;
      border: 0;
      color: white;
      padding: 0 15px;
      border-radius: 10px;
      background-color: #8A9094; }
      :host tabs li:last-of-type {
        margin: 0; }
      :host tabs li.active {
        background-color: #00329F; }

    .params-wrap {
      display: table; }

    .param-name {
      display: table-cell;
      vertical-align: top; }

    .param-info {
      display: table-cell; }

    .param {
      display: table-row; }

    .param:first-of-type .param-name:before {
      content: "";
      display: block;
      position: absolute;
      left: -1px;
      top: 0;
      border-left: 2px solid white;
      height: 20px; }

    .param:last-of-type .param-name:after {
      content: "";
      display: block;
      position: absolute;
      left: -1px;
      border-left: 2px solid white;
      top: 21px;
      background-color: white;
      bottom: 0; }
  `],
  directives: [JsonSchema]
})
export default class ParamsList extends BaseComponent {
  constructor(schemaMgr) {
    super(schemaMgr);
  }

  prepareModel() {
    this.data = {};
    let params = this.schemaMgr.getMethodParams(this.pointer, true);
    this.sortParams(params);

    // temporary handle body param
    if (params.length && params[params.length - 1].in === 'body') {
      let bodyParam = params.pop();
      bodyParam.pointer = bodyParam._pointer;
      this.data.bodyParam = bodyParam;
    }

    this.data.noParams = !(params.length || this.data.bodyParam);
    this.data.params = params;
  }

  sortParams(params) {
    const sortOrder = {
      'path' : 0,
      'query' : 10,
      'formData' : 20,
      'header': 40,
      'body': 50
    };

    params.sort((a, b) => sortOrder[a.in] - sortOrder[b.in]);
  }
}
