'use strict';

import {Component, View, EventEmitter} from 'angular2/core';
import {CORE_DIRECTIVES} from 'angular2/common';

@Component({
  selector: 'zippy',
  events: ['open', 'close'],
  inputs: ['title', 'visible', 'type', 'empty']
})
@View({
  template: `
    <div class="zippy zippy-{{type}}" [ngClass]="{'zippy-empty': empty}">
      <div class="zippy-title" (click)="toggle()">
        <span class="zippy-indicator">{{ visible ? '&#9662;' : '&#9656;' }}</span>
        {{title}}
      </div>
      <div class="zippy-content" [ngClass]="{'zippy-hidden': !visible}">
        <ng-content></ng-content>
      </div>
    </div>
  `,
  styles: [`
    .zippy-title {
      padding: 10px;
      margin: 2px 0;
      line-height: 1.5em;
      background-color: #f2f2f2;
      cursor: pointer; }
      .zippy-success > .zippy-title {
        color: #09AC1C;
        background-color: #E1F5E3; }
      .zippy-error > .zippy-title {
        color: #E54541;
        background-color: #FBE7E7; }
      .zippy-redirect > .zippy-title {
        color: #AC7C09;
        background-color: #F5F0E1; }
      .zippy-info > .zippy-title {
        color: #096DAC;
        background-color: #E1EFF5; }

    span.zippy-indicator {
      font-size: 1.5em;
      line-height: 1;
      margin-right: 0.2em;
      vertical-align: text-bottom; }

    .zippy-content {
      padding: 15px 0; }

    .zippy-empty .zippy-title {
      cursor: default; }

    .zippy-empty .zippy-indicator {
      display: none; }

    .zippy-empty .zippy-content {
      display: none; }

    .zippy-hidden {
      visibility: hidden;
      height: 0;
      padding: 0; }
  `],
  directives: [CORE_DIRECTIVES]
})
export default class Zippy {

  constructor() {
    this.type = 'general';
    this.visible = false;
    this.empty = false;
    this.open = new EventEmitter();
    this.close = new EventEmitter();
  }

  toggle() {
    this.visible = !this.visible;
    if (this.empty) return;
    (this.visible) ? this.open.next() : this.close.next();
  }
}
