'use strict';

import {RedocComponent, BaseComponent} from '../base';
import JsonPointer from '../../utils/JsonPointer';
import JsonSchema from '../JsonSchema/json-schema';
import Zippy from '../../common/components/Zippy/zippy';
import {statusCodeType} from '../../utils/helpers';

function isNumeric(n) {
  return (!isNaN(parseFloat(n)) && isFinite(n));
}

@RedocComponent({
  selector: 'responses-list',
  template: `
    <h2 class="responses-list-header" *ngIf="data.responses.length"> Responses </h2>
    <zippy *ngFor="#response of data.responses" title="{{response.code}} {{response.description}}"
      [type]="response.type" [empty]="!response.schema">
      <div *ngIf="response.headers" class="response-headers">
        <header>
          Headers
        </header>
        <div class="header" *ngFor="#header of response.headers">
          <div class="header-name"> {{header.name}} </div>
          <div class="header-type"> {{header.type}} </div>
          <div class="header-description" innerHtml="{{header.description | marked}}"> </div>
        </div>
      </div>
      <header>
        Response schema
      </header>
      <json-schema *ngIf="response.schema" class="schema type" pointer="{{response.pointer}}/schema">
      </json-schema>
    </zippy>
  `,
  styles: [`
    .responses-list-header {
      font-size: 18px;
      padding: 0.2em 0;
      margin: 0.5em 0;
      color: #253137; }

    .header-name {
      font-weight: bold;
      display: inline-block; }

    .header-type {
      display: inline-block;
      font-weight: bold;
      color: #999; }

    header {
      font-size: 14px;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: 15px; }

    .header {
      margin-bottom: 10px; }
  `],
  directives: [JsonSchema, Zippy]
})
export default class ResponsesList extends BaseComponent {
  constructor(schemaMgr) {
    super(schemaMgr);
  }

  prepareModel() {
    this.data = {};
    this.data.responses = [];

    let responses = this.componentSchema;
    if (!responses) return;

    responses = Object.keys(responses).filter(respCode => {
      // only response-codes and "default"
      return ( isNumeric(respCode) || (respCode === 'default'));
    }).map(respCode => {
      let resp = responses[respCode];
      resp.pointer = JsonPointer.join(this.pointer, respCode);
      if (resp.$ref) {
        let ref = resp.$ref;
        resp = this.schemaMgr.byPointer(resp.$ref);
        resp.pointer = ref;
      }

      resp.code = respCode;
      resp.type = statusCodeType(resp.code);
      if (resp.headers) {
        resp.headers = Object.keys(resp.headers).map((k) => {
          let respInfo = resp.headers[k];
          respInfo.name = k;
          return respInfo;
        });
      }
      resp.extendable = resp.headers || resp.length;
      return resp;
    });
    this.data.responses = responses;
  }
}
